<!--
 * @Author Demon3443002624@qq.com
 * @Version 2022-5-18 14:43:51
 * @Description 发电量统计
 * @Version 2022-5-18 14:44:14
 * @Description 
-->
<template>
  <div>
    <BMainTopComponent />
    <el-container>
      <el-main>
        <div id="crew-record-power">
          <!-- <el-row class="toggle-crew-box">
            <toggle-crew ></toggle-crew>
          </el-row> -->
          <el-row class="query-box">
            <el-card shadow="never">
              <span>筛选：</span>
              <el-row type="flex" align="middle">
                <el-select v-model="value_type" placeholder="请选择">
                  <el-option
                    v-for="item in typeOpt"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select> </el-row
              >&nbsp;&nbsp;&nbsp;&nbsp;
              <el-date-picker
                v-model="value_date"
                type="daterange"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                format="yyyy-MM-dd"
                :clearable="false"
                :popper-append-to-body="false"
                placement="bottom-start"
              >
              </el-date-picker>
            </el-card>
          </el-row>
          <el-row class="data-box">
            <el-card shadow="never">
              <el-row class="table-box">
                <el-row type="flex" justify="center">
                  <el-col class="title">发电量统计</el-col>
                </el-row>
                <el-row>
                  <el-col :span="6">机组名称</el-col>
                  <el-col :span="18">{{ selectCrew.DisplayName }}</el-col>
                </el-row>
                <el-row>
                  <el-col :span="6">开始时间</el-col>
                  <el-col :span="18">{{ dateString.start }}</el-col>
                </el-row>
                <el-row>
                  <el-col :span="6">结束时间</el-col>
                  <el-col :span="18">{{ dateString.end }}</el-col>
                </el-row>
                <el-row>
                  <el-col :span="6">累计发电量</el-col>
                  <el-col :span="18">{{ formData.energy }}</el-col>
                </el-row>
                <el-row>
                  <el-col :span="6">累计运行时间</el-col>
                  <el-col :span="18">{{ formData.time }}</el-col>
                </el-row>
              </el-row>
              <el-row class="chart-box">
                <chart :sData="sData" :xData="xData" />
              </el-row>
            </el-card>
          </el-row>
        </div>
      </el-main>
      <toggle-crew ></toggle-crew>
    </el-container>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { mapGetters } from 'vuex'

// import _data from '../data'
import Chart from './components/Chart.vue'
import ToggleCrew from '@/components/toggleCrew/list'

export default {
  name: `CrewRecordPower`,
  components: {
    ToggleCrew,
    Chart
  },
  data() {
    return {
      value_type: 1,
      value_date: [this.$moment().date(1), this.$moment()],
      typeOpt: [
        {
          label: `本月`,
          value: 1
        },
        {
          label: `前一周`,
          value: 2
        },
        {
          label: `前半个月`,
          value: 3
        },
        {
          label: `前一个月`,
          value: 4
        }
      ],
      xData: [],
      sData: [],
      formData: {
        energy: '',
        time: ''
      }
    }
  },
  computed: {
    ...mapGetters(['selectCrew']),
    dateString() {
      return {
        start: dayjs(this.value_date[0]).format('YYYY-MM-DD'),
        end: dayjs(this.value_date[1]).format('YYYY-MM-DD')
      }
    }
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.getChartData()
      },
      immediate: true,
      deep: true
    },
    value_type(nv, ov) {
      let v = null
      // 都含今日
      switch (nv) {
        case 1:
          v = [this.$moment().date(1), this.$moment()]
          break
        case 2:
          v = [this.$moment().add(-6, 'days'), this.$moment()]
          break
        case 3:
          v = [this.$moment().add(-14, 'days'), this.$moment()]
          break
        default:
          v = [this.$moment().add(-29, 'days'), this.$moment()]
          break
      }
      this.value_date = v
      this.getChartData()
    },
    value_date(nv) {
      this.getChartData()
    }
  },
  methods: {
    getChartData() {
      const p = {
          StartTime: dayjs(this.value_date[0]).format('YYYY-MM-DD'),
          EndTime: dayjs(this.value_date[1]).format('YYYY-MM-DD')
        },
        q = {
          generatorId: this.selectCrew.ID
        }
      this.$api.equipment
        .getTotalGenerationEnergy(p, q)
        .then((value) => {
          // value = _data.energy
          const dataObj = !!value && !!value.Data && value.Data
          if (dataObj) {
            const xData = dataObj.DateStrs
            const sData = dataObj.TotalEnergy
            const formData = {
              energy: dataObj.TotalYearEnergy + 'kwh',
              time: `${dataObj.TotalYearAllMinute}分钟（${dataObj.TotalYearHour}小时${dataObj.TotalYearMinute}分钟）`
            }
            this.formData = formData
            this.xData = xData
            this.sData = sData
          }
        })
        .catch((result) => {})
    }
  },
  mounted() {
    this.getChartData()
  }
}
</script>

<style lang="less" scoped>
#crew-record-power {
  .query-box {
    /deep/ .el-card {
      width: 100%;
      .el-card__body {
        display: flex;
        align-items: center;
        .el-input__inner {
          height: 40px;
        }
      }
    }
    display: flex;
  }
  .data-box {
    // margin-top: 15px;
    padding:  16px 0;
    .table-box {
      .title {
        padding: 12px 0 !important;
        font-size: 1.4rem;
        font-weight: bold;
      }
      .el-col {
        padding: 8px 0;
        display: flex;
        justify-content: center;
      }
      .el-row:nth-child(1) {
        border: 1px solid #ddd;
      }
      .el-row:nth-child(n + 2) {
        border: 1px solid #ddd;
        border-top: 0;
        :first-child {
          // background-color: #f2f2f2;
          border-right: 1px solid #f2f2f2;
        }
      }
    }
    /deep/ .el-card__body {
      padding: 0;
    }
    .chart-box {
      height: 500px;
      padding-bottom: 30px;
    }
  }
}
</style>



