<template>
  <div class="crew-record-power-chart"></div>
</template>

<script>
export default {
  name: `CrewRecordPowerChart`,
  props: {
    xData: {
      type: Array,
      default: (_) => {
        return []
      }
    },
    sData: {
      type: Array,
      default: (_) => {
        return []
      }
    }
  },
  data () {
    return {
      chart: null
    }
  },
  watch: {
    xData () {
      this.renderChart()
    },
    sData () {
      this.renderChart()
    }
  },
  methods: {
    renderChart () {
      this.chart ||
        (this.chart = this.$echarts.init(
          document.querySelector('.crew-record-power-chart')
        ))
      const xData = this.xData
      const sData = this.sData
      const opt = {
        xAxis: {
          type: 'category',
          data: xData,
          axisTick: {
            // 强制显示所有刻度
            // interval: 0
          },
          axisLabel: {
            // 强制显示所有标签
            // interval: 0,
            rotate: 60
          }
        },
        yAxis: {
          name: '累计发电量(kWh)',
          type: 'value',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#c23531'
            }
          },
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          }
        },
        tooltip: {
          show: true,
          formatter: (_) => {
            return `<el-card style="color:#fff">
                      <div>${_.name}</div>
                      <div>累计发电量：${_.data}</div>
                    </el-card>`
          },
          backgroundColor: 'rgba(50,50,50,0.7)'
        },
        series: [
          {
            // tooltip: {
            //   formatter: () => {
            //     return `eeee`
            //   }
            // },
            data: sData,
            type: 'bar',
            itemStyle: {
              color: '#d43239'
            }
            // colorBy: 'data'
          }
        ]
      }
      this.chart.setOption(opt)
    }
  }
}
</script>

<style lang="less" scoped>
.crew-record-power-chart {
  width: 100%;
  height: 100%;
}
</style>



